@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: 100%;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#root {
  height: 100%;
}
html {
    height:100%
}

body {
    /*background-color: #1BEE9A;*/
    height: 100%;
}

#background {
    height: 100%;
    background: rgb(27,238,154);
    background: linear-gradient(165deg, rgba(27,238,154,1) 48%, rgba(28,218,193,1) 100%);
}

#leftSide {
    /*width: 52vw;*/
    display: inline-block;
    float: left;
}
#rightSide {
    text-align: center;
    align-content: center;
    align-items: center;
}

#welcomeText {
    padding-left: 5vw;
    padding-top: 8vmin;
}

.welcomeTitle {
    font-weight: bold;
    font-size: max(60px, 11vmin);
}

.welcomeSubTitle {
    padding-left: 5px;
    font-weight: 500;
    font-size: max(28px, 4vmin);
}

#blob {
    position: fixed;
    bottom: -60vmin;
    left: -20vmin;
    height:100vmin;
}

#threeCharacters {
    position: fixed;
    bottom: 8vmin;
    left: 50vmin;
    width: 40vmin;
}

#loginCard {
    display: inline-block;
    background: #FFFFFF;
    border: 5px solid #229631;
    box-sizing: border-box;
    box-shadow: 0px 4px 50px 5px rgba(0, 0, 0, 0.1);
    border-radius: 20px;
    padding-top: 20px;
    padding-bottom: 20px;
    margin-top: 20vh;
    margin-bottom: 10vw;

    align-content: center;
    text-align: center;
}

form {
    width: 400px;
}

.inputBox {
    border: 0px;
    margin: 12px;
    padding: 12px;
    width: 300px;
}

input[type=submit] {
    background: #00AF91;
    box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.1);
    border-radius: 15px;
    font-weight: bold;
    font-size: 24px;
    line-height: 35px;
    padding: 10px;

    color: #FFFFFF;
}

input[type=text], input[type=password] {
    background: #F9F9F9;
    box-shadow: inset 0px 4px 10px rgba(0, 0, 0, 0.1);
    border-radius: 10px;
    font-size: 20px;
    line-height: 20px;
}

#createAccBtn {
    background: #31D747;
    box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.1);
    border-radius: 15px;
    border: 0px;
    padding: 10px 30px 10px 30px;
    font-weight: bold;
    font-size: 24px;
    line-height: 35px;

    color: #FFFFFF;
}

nav.main {
  background: rgb(255, 255, 255);
  height: 80px;
  display: flex;
  justify-content: space-between;
  padding-left: 50px;
  padding-right: 50px;
  z-index: 10;
}

.linkDefault {
  color: #000;
  display: flex;
  align-items: center;
  text-decoration: none;
  padding: 0 1rem;
  height: 100%;
  cursor: pointer;
}

.linkActive {
  color: rgb(51, 176, 214);
}

.faBars {
  display: none;
  color: rgb(0, 0, 0);
}

@media only screen and (max-width: 768px) {
  .faBars {
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    transform: translate(-100%, 75%);
    font-size: 1.8rem;
    cursor: pointer;
  }
}

.navBlock {
  display: flex;
  align-items: center;
  margin-right: -24px;
  white-space: nowrap;
}

@media only screen and (max-width: 768px) {
  .navBlock {
    display: none;
  }
}


.home {
  background-color: rgb(250, 250, 250);
  height: 100%;
}

.homeContainer {
  margin-top: 5%;
}

#feedContainer {
  margin-left: 10%;
}

#shareContainer {
  width: 18rem !important;
  border-radius: 5%;
}

#shareForm {
  width: 100%;
}

.postContainer {
  background-color: white;
  width: 30rem;
  margin-bottom: 5%;
}

.bold {
  font-weight: bold;
}

.postImage {
  width: 50%;
  height: 50%;
}
body {
  font-family: 'Roboto', sans-serif !important;
}

.app {
  height: 100%;
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

